









import { Vue, Component, Prop } from 'vue-property-decorator';
import User from '@/modules/User';

@Component
export default class UserList extends Vue {
    @Prop() value!: number;
    @Prop() perPageOptions!: number[];

    get selectPerPage() {
        return this.value;
    }

    set selectPerPage(val) {
        this.$emit('input', val);
    }
}
